body, html{
  background-color: #644F54;
}

.all-container{
  margin-top: 10%;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 50%;
}

.all-container #child{
  max-width: 80%;
  padding: 15px;
  background-color: #f1f1f1;
  text-align: center;
  -webkit-box-shadow: 5px 5px 15px 5px #5EA290; 
  box-shadow: 15px 15px 20px #5EA290;
}

.task-adder {
  display: flex;
  margin-top: 10px;
  padding: 5px 0 5px 0;
  justify-content: space-between;
}

.container-task-show {
  margin: 10px 0 10px 0;
  padding: 5px 0 5px 0;
  border: 1.5px solid #AC397C;
}

.container-task-show #taskdeleteshow {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
}